import React from 'react';
import { Helmet } from 'react-helmet';

function BlogInner() {
  return (
    <div>
    <Helmet>
        <title>Scritpics | Blog</title>
        <meta name="description" content="Our company case-studies."/>
        <meta name="twitter:card" content="summary_large_image"/>
        <meta name="twitter:site" content="@Scripticstech"/>
        <meta name="twitter:title" content="Scritpics | Case-studies"/>
        <meta name="twitter:description" content="Our company case-studies"/>
        <meta name="twitter:creator" content="@Scripticstech"/>
        <meta property="og:type" content="website"/>
        <meta property="og:title" content="Scritpics | Case-studies"/>
        <meta property="og:description" content="Our company case-studies"/>
        <meta property="og:image" content="https://www.scriptics.ai/assets/img/og_meta.png"/>
        <meta property="og:url" content="https://www.scriptics.ai/"/>
        <meta name="twitter:image" content="https://www.scriptics.ai/assets/img/1674040738302.png" />
        <meta name="twitter:domain" content="scriptics.ai"/>
    </Helmet>
    <section className='blog-banner'>
        <div className='container'>
            <div className='row'>
                <div className='col-md-9 col-sm-12 col-xs-12'>
                    <h1 className='h2-heading white-color mb-xs-10'>Press Releases</h1>
                </div>
            </div>
        </div>
    </section>
    <section className='press-release'>
        <div className='container'>
            <div className='row'>
                <div className='col-md-12 col-sm-12 col-xs-12' >
                    <h1 className='h2-heading'>Nine Out of Ten Companies Lack the Culture and Organizational Structure to Unlock Digital Growth, Scripticss Report Finds</h1>
                    <p className='blog-inner-para'>Scripticss research reveals a blueprint for the 21st century enterprise focused on live data, responsible risk-taking and product-centricity</p>
                    <p className='blog-date'>Anand, India: March 7, 2023</p>
                    <div className='col-md-8 col-md-offset-2 col-sm-12 col-xs-12 mt50 blog-para'>
                        <p>Only 7% of companies have the correct combination of culture and operating structure to boost growth from digital technologies, according to new research from the Scripticss Knowledge Institute, the thought leadership arm of Scripticss (NSE, BSE, NYSE: INFY), a global leader in next-generation digital services and consulting.</p>
                        <p><strong>Digital Radar: The Next Digital Frontier</strong>, surveyed 2,700 business executives across the US, UK, France, Germany, Australia, New Zealand, China, and India, and found that companies that make decisions based on high quality, transparent data and build a culture of responsible risk-taking are more likely to deliver profitable growth in today’s tough macro-economic conditions. In addition, organizing around products, not process, gets new products to market faster, increasing early-mover advantage.</p>
                        <p>This research suggests three differentiators for success: use data internally, design organization culture to take responsible risks and organize the business around products. Companies that excel in these capabilities enjoy increased profit, brand perception, and employee as well as customer engagement. However, our report found that less than 10% of firms have mastered these three differentiators, highlighting significant opportunities for those that do.</p>
                        <p><strong>Live Data:</strong> The report finds that only 5% of firms currently implement what it defines as a universal ‘live data’ approach. This is defined as data that is high-quality, timely, and readily available across the organisation. These data practices drive profit through better innovation and new product speed to market. For instance, companies that follow robust data practices for decision-making improved their new product introduction capability by as much as 85%.</p>
                        <p><strong>Product-centricity: </strong>Companies that organize their teams around their product offerings, instead of business functions, are 50% more likely to be top performers in new product introduction and improve both employee and customer engagement significantly. Yet only half of respondent companies are currently organized in this way, highlighting a tremendous market opportunity.</p>
                        <p><strong>Responsible risk-taking culture:</strong> Finally, a company that takes responsible, considered risks, backed by live data, is more likely to create products faster, retain employees, and increase profit than those that do not. We identified five culture levers that can improve innovation fivefold: flexible leadership style, leadership and employee diversity, data-driven leadership, rapid test-and-learn, and encouraging risk-taking.</p>
                        <div class="blog-line"></div>
                        <h3>Methodology</h3>
                        <p>Scripticss Knowledge Institute used an anonymous format to conduct an online survey of 2,700 business executives across industries across the US, UK, France, Germany, Australia, New Zealand, China, and India. To gain additional, qualitative insights, the researchers interviewed subject matter experts and business leaders. Linear regressions were then used to analyze which technologies and business practices correlate with profit and revenue growth.</p>
                        <div class="blog-line"></div>
                        <h3>About Scripticss</h3>
                        <p>Scripticss is a global leader in next-generation digital services and consulting. Over 300,000 of our people work to amplify human potential and create the next opportunity for people, businesses and communities. With over four decades of experience in managing the systems and workings of global enterprises, we expertly steer clients, in more than 50 countries, as they navigate their digital transformation powered by the cloud. We enable them with an AI-powered core, empower the business with agile digital at scale and drive continuous improvement with always-on learning through the transfer of digital skills, expertise, and ideas from our innovation ecosystem. We are deeply committed to being a well-governed, environmentally sustainable organization where diverse talent thrives in an inclusive workplace.</p>
                        <div class="blog-line"></div>
                        <h3>Safe Harbor</h3>
                        <p>Certain statements in this release concerning our future growth prospects, financial expectations and plans for navigating the COVID-19 impact on our employees, clients and stakeholders are forward-looking statements intended to qualify for the 'safe harbor' under the Private Securities Litigation Reform Act of 1995, which involve a number of risks and uncertainties that could cause actual results to differ materially from those in such forward-looking statements. The risks and uncertainties relating to these statements include, but are not limited to, risks and uncertainties regarding COVID-19 and the effects of government and other measures seeking to contain its spread, risks related to an economic downturn or recession in India, the United States and other countries around the world, changes in political, business, and economic conditions, fluctuations in earnings, fluctuations in foreign exchange rates, our ability to manage growth, intense competition in IT services including those factors which may affect our cost advantage, wage increases in India and the US, our ability to attract and retain highly skilled professionals, time and cost overruns on fixed-price, fixed-time frame contracts, client concentration, restrictions on immigration, industry segment concentration, our ability to manage our international operations, reduced demand for technology in our key focus areas, disruptions in telecommunication networks or system failures, our ability to successfully complete and integrate potential acquisitions, liability for damages on our service contracts, the success of the companies in which Scripticss has made strategic investments, withdrawal or expiration of governmental fiscal incentives, political instability and regional conflicts, legal restrictions on raising capital or acquiring companies outside India, unauthorized use of our intellectual property and general economic conditions affecting our industry and the outcome of pending litigation and government investigation. Additional risks that could affect our future operating results are more fully described in our United States Securities and Exchange Commission filings including our Annual Report on Form 20-F for the fiscal year ended March 31, 2022. These filings are available at www.sec.gov. Scripticss may, from time to time, make additional written and oral forward-looking statements, including statements contained in the Company's filings with the Securities and Exchange Commission and our reports to shareholders. The Company does not undertake to update any forward-looking statements that may be made from time to time by or on behalf of the Company unless it is required by law.</p>
                    </div>
                </div>
            </div>
        </div>
    </section>
    </div>
  )
}

export default BlogInner